/* table style */
.card-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: auto;
    grid-gap: 1rem;
}
.card-boody
{
    border: 2px;
    border-radius: 4px;
    padding: .5rem;
    text-align: center;
    background: rgb(107, 102, 146) !important;
    color: white !important;
    box-shadow:rgba(0, 0, 0, 0.479) !important ;
}
.card-boody:hover{
    background: rgb(55, 53, 80) !important;
    transition: 0.6s;
}


.more{
    margin: 0 auto;
}
.icon-delete{
    right: 50px;
}
